async function getAddress (walletName) {
  if (walletName === 'metamask') {
    console.log(' metamask');
    const accounts = await window.ethereum.enable();
    // caohontao: web3 reducer 6 
    // this.props.fetchAccounts();
    let address = accounts[0]
    if (!address) {
      console.log(`metamask `);
      return Promise.reject('')
    }
    console.log(`metamask : ${address}`);

    window.wallet = ethereum.account.fromMetaMask(window.web3, address, address)
    return address
  } else if (walletName === 'dkey') {
    console.log(' dkey');
    let promise = new Promise((resolve, reject) => {
      window.dkeyEnableSuccess = function (resp) {
        resolve(resp.result)
      }
      window.dkeyEnableFailure = function (error) {
        reject(error)
      }
      window.ethereum.enable('dkeyEnableSuccess', 'dkeyEnableFailure')
    })
    const address = await promise
    if (!address) {
      console.log(`dkey `);
      return Promise.reject('')
    }
    console.log(`dkey : ${address}`);

    window.wallet = ethereum.account.fromDkey(window.web3, address, address)
    return address
  }
}

